.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.mask {
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 9999;
}
.mask .text {
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 30px;
    font-weight: bold;
    transform: translate(-50%, -50%);
    width: 300px;
  }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.modal {
  position: 'absolute';
  top: '50%';
  left: '50%';
  transform: 'translate(-50%, -50%)';
  width: 400;
  align: 'center';
  border: '2px solid #000';
  pt: 2;
  px: 4;
  pb: 3;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h3 {
  margin: 5px;
}
h1 {margin: 5px}
hr {
  margin: 0px;
}
@import url("https://fonts.googleapis.com/css?family=Aldrich");
.normal {
  margin-x:50px;
  margin-top: 200px;
  font-size: 120px;
  font-family: Aldrich;
}
.village {
  margin-x:50px;
  font-size: 120px;
  font-family: Aldrich;
}
.mini {
  position: fixed;
  bottom: 20px;
  width: 110px;
  z-index: 2;
  left: 20px;
}
.clock-value {
  font-family: Aldrich;
  border-style: solid;
  border-color: black;
  border-radius: 10px;
  font-size: 30px;
  padding: 5px;
  height: 38px;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

.clock {
  font-family: Aldrich;
  border-color: black;
  font-size: 80px;
  padding: 5px;
  justify-content: center;
  display: flex;
  height: 100px;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

