*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.skw-pages {
  overflow: hidden;
  position: relative;
  height: 100vh;
}

$scrollTime: 1s;

.skw-page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;

  &__half {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    transition: transform $scrollTime;
    transform: translate3d(0, 100%, 0);
    .skw-page.active & {
      transform: translate3d(0,0,0);
    }
  }
}
